@import '../../scss/mixins';
@import '../../scss/variables';


.Country {
    width: 100%;
    background-color: var(--color-element);
    border-radius: 1rem;
    box-shadow: .3rem .3rem 1rem 0 rgba(0,0,0,.15);
    cursor: pointer;
    // height: 45rem;
    transform: translate(0, 0);
    padding: 2rem 2rem 0rem 2rem;

    @include mwbreakpoint(410px) {
        padding: 1rem 1rem 0rem 1rem;
    }


    &__flag {
        width: 100%;
        max-height: 22.5rem;
        border-radius: 1rem;
        box-shadow: 0 .3rem .5rem 0 rgba(0,0,0,.15);
        // object-fit: cover;
        // object-position: center;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    &__flag-container {
        width: 31rem;
        height: 22.5rem;
        position: relative;
        flex-shrink: 0;

        @include mwbreakpoint(410px) {
            width: 30rem;
        }
        @include mwbreakpoint(350px) {
            width: 22rem;
        }


    }

    &__text {
        // padding: 2rem;
        padding-top: 3rem;
        transform: translate(0, 0);

        @include mwbreakpoint(410px) {
            padding-top: 1rem;
        }
    }

    &__name {
        font-size: 2.4rem;
        margin-bottom: 1.4rem;
        font-weight: 800;
    }

    &__details {
        font-size: 1.9rem;
        margin-bottom: 4rem;
    }

    &__info-title {
        font-weight: 600;

    }

    &__info {

        &:not(:last-child) {
            margin-bottom: .5rem;
        }
    }

    &__info--full {
        display: none;
    }



    &__boxes {
        display: none;
        flex-wrap: wrap;
    }

    &__box {
        margin-right: 1rem;
        margin-bottom: .8rem;
        background: var(--color-element);
        box-shadow: 0 .1rem .5rem 0 rgba(0,0,0,.15);
        width: 12rem;
        padding: .4rem 0;
        font-size: 1.4rem;
        font-weight: 600;
        color: var(--color-text);

        &:focus {
            outline: none;
        }
    }

    &__back {
        display: none;
        cursor: pointer;
        align-items: center;
        justify-content: center;
        & > * {
            margin-right: 1rem;
        }
        position: absolute;
        top: 3.2rem;
        left: 3.2rem;
        color: var(--color-text);

        svg path {
            fill: var(--color-text);;
        }

        @include breakpoint($bp-tablet) {
            left: 9.7rem;
        }

        @include breakpoint($bp-desktop) {
            left: 16.5rem;
        }



    }

    &--moving {
        width: 100vw;
        height: 100vh;
        z-index: 6;
        border-radius: 0;
        transition: all .5s;
        background-color: var(--color-bg);

        .Country {

            &__flag {
                border-radius: 1rem;
                top: 0;
                transform: translate(-50%, 0);
                transition: all .5s;

                @include breakpoint($bp-tablet) {
                    max-height: 26.6rem;
                }

                @include breakpoint($bp-tablet) {
                    max-height: 33.3rem;
                }
            }

            &__flag-container {
                transform: translate(1.2rem, 10rem);
                transition: transform .5s, width .5s, height .5s;

                @include mwbreakpoint(410px) {
                    width: 30rem;
                    transform: translate(2.2rem, 11rem);
                }
                @include mwbreakpoint(350px) {
                    width: 22rem;
                }

                @include breakpoint($bp-tablet) {
                    width: 40rem;
                    height: 26.6rem;
                    transform: translate(9.7rem, 13rem);
                }

                @include breakpoint($bp-desktop) {
                    width: 50rem;
                    height: 33.3rem;
                    transform: translate(16.5rem, 13rem);
                }
            }

            &__text{
                transition: all .5s;
                transform: translate(1.2rem, 10rem);

                @include mwbreakpoint(410px) {
                    transform: translate(2.2rem, 11rem);
                }

                @include mwbreakpoint(350px) {
                    // width: 22rem;
                }


                

                @include breakpoint($bp-tablet) {
                    padding: 0;
                    transform: translate(9.7rem, 18rem);
                }

                @include breakpoint(1100px) {
                    padding: 0;
                    transform: translate(55.7rem, -13.7rem);
                }

                @include breakpoint($bp-desktop) {
                    transform: translate(72.5rem, -20.3rem);
                }
            }
            &__stats {
                display: grid;
                column-gap: 4rem;
                grid-template-columns: repeat(auto-fit, minmax(25rem, max-content));          
            }
        }
    }






    &--full {
        z-index: 6;
        width: 100vw;
        height: 100vh;
        position: fixed;
        top: 8.6rem;
        left: 0;
        border-radius: 0;
        padding: 3.2rem;
        cursor: initial;
        overflow: auto;
        background-color: var(--color-bg);


        @include breakpoint($bp-tablet) {
            padding: 15rem 9.7rem 15rem 9.7rem;

        }

        @include breakpoint(1100px) {
            padding: 15rem 9.7rem 15rem 9.7rem;
            display: flex;
            align-items: flex-start;

        }

        @include breakpoint($bp-desktop) {
            padding: 15rem 16.5rem 15rem 16.5rem;

        }


        & .Country {   

            &__flag {
                top: 0;
                transform: translate(-50%, 0);
                border-radius: 1rem;

                @include breakpoint($bp-tablet) {
                    max-height: 26.6rem;
                }

                @include breakpoint($bp-tablet) {
                    max-height: 33.3rem;
                }
                

            }

            &__flag-container {
                margin-top: 8.8rem;
                margin-right: 6rem;
                align-items: flex-start;
                transition: none;

                @include breakpoint($bp-tablet) {
                    margin-top: 0;
                    width: 40rem;
                    max-width: none;
                    height: 26.6rem;
                    max-height: none;
                }

                @include breakpoint($bp-desktop) {
                    width: 50rem;
                    height: 33.3rem;
                }
            }

            &__text{
                flex: 1 1 auto;
                max-width: 70rem;

                @include breakpoint($bp-tablet) {
                    padding-top: 5rem;
                }

                @include breakpoint(1100px) {
                    padding: 0;
        
                }

                @include breakpoint($bp-desktop) {
                    padding: 0;
                }
            }
            &__stats {
                display: grid;
                column-gap: 4rem;
                grid-template-columns: repeat(auto-fit, minmax(20rem, max-content));          
            }

            &__info--full {
                display: block;
            }

            &__back {
                display: block;
            }

            &__boxes {
                display: flex;
            }

            &__back {
                display: flex;
            }
            
        }
    }
}



